const regex_validate = {
  email: (value) =>
    /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(value) ? null : "Email invlido!",
  name: (value) =>
    value.length >= 3 && value.length <= 50 ? null : "Tamanho invlido [3..50]",
  keywords: (value) =>
    value.length >= 1 && value.length <= 20
      ? null
      : "Quantidade de palvras [1..20]",
};

export default regex_validate;
